body{
  background-color: #9266ff;
}

.App{
  background-color: #3f3160;
  background-image: linear-gradient(#2f2741, #9266ff);
  min-height:500px;
  width: 400px;
  margin: 40px auto;
  box-shadow: 0px 4px 11px 3px darkslategrey;
}

.container {
  background-color: #2f2741;
  max-width: 100% !important;
}

#to-do-form input{
  background-color: rgba(27, 112, 137, 1);
  border: 0; 
  color: #fff;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder{
  color: rgba(255,255,255,0.5);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:rgba(202, 156, 24, 1); 
  cursor: pointer;
}
#to-do-form button:focus{
  outline: none;
}